@import "fonts.css";
@import "normalize.css";


* {
  box-sizing: border-box;
}

html {
  font-size: 87.5%;
}

body,
html {
  height: 100%;
  margin: 0;
  font-family:
    'Source Sans Pro',
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Noto,
    DroidSans,
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    "Open Sans",
    "Helvetica Neue",
    Helvetica,
    Arial,
    "Apple Color Emoji",
    "Segoe UI Emoji",
    "Segoe UI Symbol",
    "Adobe Arabic",
    "Myriad Arabic",
    " Pro W3",
    "Hiragino Kaku Gothic Pro",
    "",
    Meiryo,
    " ",
    "MS PGothic",
    sans-serif;
  color: var(--color-text);
  letter-spacing: 0.04em;
  -webkit-font-smoothing: antialiased;
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  body,
  html {
    font-family:
      'Source Sans Pro',
      -apple-system,
      BlinkMacSystemFont,
      "Segoe UI",
      Noto,
      DroidSans,
      Roboto,
      Oxygen,
      Ubuntu,
      Cantarell,
      "Open Sans",
      "Helvetica Neue",
      Helvetica,
      Arial,
      "Apple Color Emoji",
      "Segoe UI Emoji",
      "Segoe UI Symbol",
      "Adobe Arabic",
      "Myriad Arabic",
      " Pro W3",
      "Hiragino Kaku Gothic Pro",
      "",
      Meiryo,
      " ",
      "MS PGothic",
      sans-serif;
  }
}

/* global "print" styles below was added for solve the problem with extra blank page generating on printing in all modules */
@media print {
  html, body {
    margin: 0 !important;
  }

  @page {
    margin: var(--page-margin-default);
  }
}

a {
  color: var(--color-link);
  text-decoration: none;

  &:visited {
    color: var(--color-link-visited);
  }
}

strong {
  font-weight: var(--text-weight-bold);
}

fieldset {
  border: none;
  padding: 0;
}

ul {
  margin: 0 0 14px;
}

hr {
  border: 1px solid var(--color-border-p2);
  border-width: 0 0 1px;
}

button,
input,
textarea,
select {
  font-size: inherit;
  font-family: inherit;
  letter-spacing: inherit;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;

  &:hover {
    cursor: pointer;
  }
}

/* clears the X from Internet Explorer */
input[type="search"]::-ms-clear {
  display: none;
  width : 0;
  height: 0;
}

input[type="search"]::-ms-reveal {
  display: none;
  width : 0;
  height: 0;
}

/* clears the X from Chrome */
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration { display: none; }

/* displays number spinners on non-focused in Chrome */
input[type="number"]::-webkit-inner-spin-button {
  opacity: 1;
}

:global(.fullWidth) {
  width: 100% !important;
}

:global(.marginTop0) {
  margin-top: 0 !important;
}

:global(.marginBottom0) {
  margin-bottom: 0 !important;
}

:global(.marginBottomHalf) {
  margin-bottom: 0.5rem !important;
}

:global(.marginTopHalf) {
  margin-top: 0.5rem !important;
}

:global(.floatEnd) {
  float: right !important;
}

:global(.sr-only) {
  position: fixed;
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
  height: 0;
  width: 0;
  overflow: hidden;
}

:global(.tether-element) {
  z-index: 9999;
}

:global(.editor-preview *) {
  margin: 0;
}

:global(body[dir="rtl"] .floatEnd),
:global(html[dir="rtl"] .floatEnd) {
  float: left !important;
}
